@font-face {
  font-family: 'fontello';
  src: url('../assets/fontello/font/fontello.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.ngel-icon {
  &:before {
    font-family: 'fontello';
    font-style: normal;
    font-weight: normal;
    speak: none;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: 0.2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;

    // fix buttons height, for twitter bootstrap
    line-height: 1em;

    // Animation center compensation - margins should be symmetric
    // remove if not needed
    margin-left: 0.2em;

    // Font smoothing. That was taken from TWBS
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  &--reverse:before { transform: scaleX(-1); }
}

// Icon Sizes
// -------------------------

// makes the font 33% larger relative to the icon container
.ngel-icon--size-large {
  font-size: (4em / 3);
  line-height: (3em / 4);
  vertical-align: -15%;
}
.ngel-icon--size-2x { font-size: 2em; }
.ngel-icon--size-3x { font-size: 3em; }
.ngel-icon--size-4x { font-size: 4em; }
.ngel-icon--size-5x { font-size: 5em; }

.ngel-icon--color {
  &-danger { color: $color-ui-highlight-danger; }
  &-success { color: $color-ui-highlight-success; }
  &-warning { color: $color-ui-highlight-warning; }
  &-muted { color: $color-ui-primary-gray; }
  &-light { color: $color-ui-text-light; }
}

// Icon Animations
//
// To make your icon spin,
// just add the `ns-icon--spin` class.
//
// Markup: spinners.html
//
// Styleguide icons.spinner

.ngel-icon--spin {
  animation: spin 2s infinite linear;
  display: inline-block;
}

@-moz-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(359deg); }
}
@-webkit-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(359deg); }
}
@-o-keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(359deg); }
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(359deg); }
}

@import '../assets/fontello/fontello-codes';


